/* ==== Global Theme (Gemma) =============================================== */
:root{
  --bg: #0b1220;           /* deep navy */
  --panel: #101a2d;        /* card */
  --panel-2:#0e1830;
  --text: #e6ecff;         /* main text */
  --muted:#9bb1ff;         /* subtle text */
  --accent:#35d0ba;        /* mint/teal */
  --accent-2:#45a3ff;      /* secondary accent */
  --border:#1c2a49;
  --shadow: 0 10px 30px rgba(0,0,0,.35);
  --radius: 16px;
}

*{box-sizing:border-box}
html,body,#root{height:100%}
body {
  margin: 0;
  background: radial-gradient(circle at 20% 20%, #1b2949, transparent 50%),
              radial-gradient(circle at 80% 10%, #2f3b66, transparent 50%),
              radial-gradient(circle at 40% 90%, #132b47, transparent 60%),
              linear-gradient(180deg, #0b1220, #0e182e);
  background-attachment: fixed;
  color: var(--text);
  font-family: ui-sans-serif, system-ui, Segoe UI, Roboto, Ubuntu, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/* ==== App Shell ========================================================== */
.App, .app{
  max-width: 1100px;
  margin: 0 auto;
  padding: 28px 22px 40px;
}
header{
  display:flex; align-items:flex-end; justify-content:space-between;
  gap:16px; padding:8px 0 18px;
}
h1{margin:0; font-size: 32px; line-height:1.1; letter-spacing:.2px}
h1 .brand{color:var(--accent); font-weight:800}
.tag{margin:6px 0 0; color:var(--muted); font-size:14px}

/* Layout: two columns on desktop, one on mobile */
.columns{
  display:grid; grid-template-columns: 1fr 1fr; gap:18px; align-items:start;
}
@media (max-width: 900px){ .columns{ grid-template-columns:1fr } }

/* ==== Cards ============================================================= */
.card, .SearchResults, .Playlist{
  background: linear-gradient(180deg, var(--panel) 0%, var(--panel-2) 100%);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 16px;
  box-shadow: var(--shadow);
}

.card h2, .Playlist h2{ margin: 4px 0 12px; font-size: 18px }

/* ==== Buttons =========================================================== */
.btn, .Track-action, .Playlist-save, .SearchBar-submit{
  appearance:none; border:none; cursor:pointer;
  padding:10px 14px; border-radius:12px;
  background: var(--panel-2); color: var(--text);
  border:1px solid var(--border);
  transition: transform .06s ease, filter .2s ease, background .2s ease;
}
.btn.primary, .Playlist-save{
  background: linear-gradient(180deg, var(--accent) 0%, #1bbfa9 100%);
  color:#05221c; border:none; font-weight:700;
}
.btn:hover, .Track-action:hover, .Playlist-save:hover{
  transform: translateY(-1px);
  filter: brightness(1.08);
}
.btn.ghost{ background:transparent; border:1px solid var(--border); }

/* Small action buttons (+ / -) on tracks */
.Track-action{
  width:36px; height:36px; border-radius:10px; font-size:20px; line-height:1;
  display:flex; align-items:center; justify-content:center;
}

/* ==== Search Bar ======================================================== */
.SearchBar, .searchbar{
  display:flex; gap:10px; margin: 14px 0 18px;
}
.SearchBar input, .searchbar input{
  flex:1; padding:12px 14px;
  background:#0d1529; color:var(--text);
  border-radius:12px; border:1px solid var(--border);
  outline:none;
}
.SearchBar input:focus{ box-shadow: 0 0 0 2px rgba(53,208,186,.25) }

/* ==== Track List ======================================================== */
.TrackList, .tracklist{
  max-height: 58vh; overflow:auto; padding-right:4px;
}
.Track, .track{
  display:flex; align-items:center; justify-content:space-between; gap:12px;
  padding:12px 2px; border-bottom:1px solid var(--border);
}
.Track:last-child, .track:last-child{ border-bottom:none }
.Track-information, .track-info{
  min-width:0;
}
.Track-information h3, .track-name{
  margin:0 0 4px; font-size:15px; font-weight:700; color:#e9f0ff;
  white-space:nowrap; overflow:hidden; text-overflow:ellipsis;
}
.Track-information p, .track-meta{
  margin:0; color:#9fb0d1; font-size:12px;
  white-space:nowrap; overflow:hidden; text-overflow:ellipsis;
}

/* ==== Playlist Panel ==================================================== */
.Playlist input, .playlist-name{
  width:100%; margin:8px 0 12px; padding:12px 14px;
  border-radius:12px; border:1px solid var(--border);
  background:#0d1529; color:var(--text); outline:none;
}
.Playlist-save{
  display:inline-flex; align-items:center; gap:8px; margin-top:10px;
}

/* ==== Footer ============================================================ */
footer{
  margin-top:24px; color:#8ea2d7; font-size:13px; display:flex; gap:8px; align-items:center;
}
footer a{ color:var(--muted); text-decoration:none }
footer a:hover{ text-decoration:underline }

