
/* Gemma theme: deep navy, mint accent, clean cards */
:root { --bg:#0b1220; --card:#101a2d; --text:#e6ecff; --muted:#9bb1ff; --accent:#35d0ba; }
body,
html,
#root {
  height: 100%;
}

html {
  font-size: 18px;
}

h1 {
  padding: .77rem 0;
  background-color: #010c3f;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 1.88rem;
  color: #fff;
}

h1 .highlight {
  color: #6c41ec;
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.55rem;
}

.App {
  min-height: 100%;
  padding: 0 17% 10% 17%;
  /* NEW modern background (no image) */
  background:
    radial-gradient(circle at 20% 20%, #1b2949 0%, transparent 50%),
    radial-gradient(circle at 80% 10%, #2f3b66 0%, transparent 50%),
    radial-gradient(circle at 40% 90%, #132b47 0%, transparent 60%),
    linear-gradient(180deg, #0b1220 0%, #0e182e 100%);
  background-attachment: fixed;
  background-size: cover;
  background-position: center;

  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}



.App-playlist {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}