.SearchBar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  margin: 1.5rem auto;
}

.SearchBar input {
  width: 320px;
  padding: 0.8rem 1rem;
  border-radius: 25px;
  border: none;
  outline: none;
  font-size: 1rem;
  color: var(--text);
  background-color: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.15);
  transition: all 0.2s ease-in-out;
}

.SearchBar input:focus {
  box-shadow: 0 0 6px var(--accent);
}

.SearchButton {
  background-color: var(--accent);
  color: #0b1220;
  font-weight: 600;
  border: none;
  border-radius: 25px;
  padding: 0.8rem 1.8rem;
  cursor: pointer;
  transition: all 0.25s ease;
}

.SearchButton:hover {
  background-color: #4ff7e0;
  transform: translateY(-2px);
}
